x-width: 100%;\n        font-size: $dg-font-size-base;\n        th,\n        td {\n            border: 1px solid $dg-table-border-color;\n            padding: 8px 14px;\n            p {\n                margin: 0px;\n            }\n        }\n\n        th {\n            background: $dg-table-header-bg-color;\n            text-align: center;\n        }\n\n        td {\n            vertical-align: middle;\n        }\n    }\n}\n\n.dg-doc-meta {\n    display: flex;\n    flex: 0 0 auto;\n    align-items: center;\n    padding: 25px 0px;\n\n    .dg-contributor-avatar-list {\n        display: flex;\n        flex-direction: row-reverse;\n        margin-right: 8px;\n    }\n    .dg-contributor-avatar {\n        box-shadow: 0 0 0 2px $dg-white;\n        width: 30px;\n        height: 30px;\n        border-radius: 30px;\n        overflow: hidden;\n        img {\n            width: 100%;\n        }\n    }\n    .dg-contributor-avatar:nth-child(n):not(:first-child) {\n        margin-left: -8px;\n    }\n    .dg-contributor-avatar:nth-child(1) {\n        order: 3;\n    }\n    .dg-contributor-avatar:nth-child(2) {\n        order: 2;\n    }\n    .dg-contributor-avatar:nth-child(3) {\n        order: 1;\n    }\n    .dg-contributor-other {\n        display: flex;\n        align-items: center;\n        justify-content: center;\n        color: $dg-gray-500;\n        background-color: $dg-gray-100;\n    }\n    .dg-contribution-last-time {\n        color: $dg-gray-500;\n    }\n}\n\n.dg-pages-link {\n    display: flex;\n    .dg-page-link {\n        flex: 1 1 0;\n        width: 100%;\n        display: flex;\n        a {\n            padding: 16px;\n            border-radius: 3px;\n            color: $dg-gray-800;\n            border: 1px solid $dg-gray-200;\n            box-shadow: rgb(116 129 141 / 10%) 0px 3px 8px 0px;\n            width: 100%;\n            display: flex;\n            &:hover {\n                text-decoration: none;\n                color: $dg-primary;\n                border-color: $dg-primary;\n                cursor: pointer;\n                .dg-page-link-icon {\n                    color: $dg-primary;\n                }\n            }\n            .dg-page-link-icon {\n                color: $dg-gray-500;\n                flex: 1 1 0;\n                align-self: center;\n                dg-icon {\n                    font-size: 24px;\n                }\n            }\n        }\n        &:first-child {\n            a {\n                text-align: right;\n            }\n            &:not(:last-child) {\n                margin-right: 24px;\n            }\n            .dg-page-link-icon {\n                text-align: left;\n            }\n        }\n\n        &:last-child {\n            a {\n                text-align: left;\n                flex-direction: row-reverse;\n            }\n            .dg-page-link-icon {\n                text-align: right;\n            }\n        }\n\n        .dg-page-link-label {\n            font-size: 12px;\n            color: $dg-gray-600;\n            font-weight: 400;\n            line-height: 1.625;\n        }\n        .dg-page-link-title {\n            font-size: 16px;\n            font-weight: 500;\n            line-height: 1.5;\n        }\n    }\n}\n",".dg-icon {\n    font-size: 1rem;\n    line-height: 0;\n    visibility: visible;\n    svg {\n        width: 1em;\n        height: 1em;\n        fill: currentColor;\n        vertical-align: -.16em;\n        background-repeat: no-repeat;\n    }\n}\n","// $dg-danger-color: #d81b60;\n// $dg-primary-color: #348fe4;\n\n.dg-label {\n    display: inline-flex;\n    align-items: center;\n    color: $dg-white;\n    padding: 4px 10px;\n    border-radius: 4px;\n    font-size: $dg-font-size-base;\n    margin-bottom: 0px;\n    font-size: $dg-font-size-sm;\n    line-height: 1;\n}\n\n$dg-theme-colors: map-merge(\n    (\n        'default': $dg-gray-200,\n        'light': $dg-gray-700\n    ),\n    $dg-theme-colors\n);\n\n@each $name, $color in $dg-theme-colors {\n    .dg-label-#{$name} {\n        background-color: $color;\n        color: dg-color-yiq($color);\n    }\n}\n\n@each $name, $color in $dg-theme-colors {\n    .dg-label-outline-#{$name} {\n        @include label-outline($color);\n    }\n}\n\n.dg-label-default,\n.dg-label-light,\n.dg-label-outline-default,\n.dg-label-outline-light {\n    color: $dg-gray-700;\n}\n\n.dg-label-sm {\n    padding: 2px 8px;\n    font-size: $dg-font-size-sm;\n}\n","@mixin label-outline($color) {\n    color: $color;\n    background-color: transparent;\n    border: 1px solid $color;\n}\n","$dg-alert-bg-alpha: 0.05;\n\n.dg-alert {\n    position: relative;\n    display: block;\n    font-size: 14px;\n    line-height: 20px;\n    margin: 10px 0px;\n    padding: 10px 20px;\n    border-left: 0px;\n    background: $dg-gray-80;\n    border-radius: 1px;\n\n    &::after {\n        content: '';\n        position: absolute;\n        display: inline-block;\n        top: 0px;\n        left: 0px;\n        bottom: 0px;\n        width: 3px;\n        border-radius: 1px;\n    }\n}\n\n@each $name, $color in $dg-theme-colors {\n    .dg-alert-#{$name} {\n        background-color: rgba($color: $color, $alpha: $dg-alert-bg-alpha);\n        &::after {\n            background: $color;\n        }\n    }\n}\n",".dg-toc {\n    flex: 1;\n    position: sticky;\n    top: 20px;\n    margin: 20px 0 20px 20px;\n\n    .dg-toc-container {\n        font-size: 13px;\n        // Width is container width minus content width\n        width: 19%;\n        // position: sticky;\n        // top: 0;\n        padding-left: 10px;\n        box-sizing: border-box;\n        display: inline-flex;\n        flex-direction: column;\n        width: 100%;\n\n        & + .docs-toc-container {\n            padding-top: 8px;\n        }\n    }\n\n    .dg-toc-heading {\n        margin: 0;\n        padding: 6px 12px 6px 12px;\n        font-size: 13px;\n        font-weight: bold;\n        text-transform: capitalize;\n    }\n\n    ul {\n        margin: 0;\n        padding: 0;\n        list-style: none;\n        li {\n            list-style: none;\n            position: relative;\n            padding: 0px;\n            &[data-level='1'] {\n                padding-left: 12px;\n            }\n\n            &[data-level='2'] {\n                padding-left: 24px;\n            }\n\n            &[data-level='3'] {\n                padding-left: 36px;\n            }\n\n            &[data-level='4'] {\n                padding-left: 48px;\n            }\n\n            a {\n                color: $dg-gray-700;\n            }\n        }\n    }\n\n    .dg-link {\n        padding: 6px 0px;\n        color: $dg-gray-700;\n        line-height: 16px;\n        text-decoration: none;\n        display: block;\n        text-overflow: ellipsis;\n        width: 100%;\n        box-sizing: border-box;\n\n        &::before {\n            border-left: 2px solid $dg-gray-300;\n            bottom: 0;\n            content: '';\n            left: 0;\n            position: absolute;\n            top: 0;\n        }\n\n        &.active {\n            color: darken($dg-primary, 10%);\n            &::before {\n                border-color: $dg-primary;\n                border-width: 2px;\n            }\n        }\n    }\n\n    &-menu {\n        .dg-toc-container {\n            padding-left: 20px;\n        }\n        .dg-link {\n            position: relative;\n            // padding: 6px 6px 6px 6px;\n            &::before {\n                border-width: 0px;\n            }\n            &.active {\n                &::before {\n                    border-width: 0px;\n                    height: 5px;\n                    width: 5px;\n                    border-radius: 50%;\n                    background: $dg-primary;\n                    top: 12px;\n                    left: -10px;\n                }\n            }\n        }\n    }\n}\n",".dg-btn {\n    display: inline-block;\n    color: $dg-body-color;\n    text-align: center;\n    vertical-align: middle;\n    cursor: pointer;\n    user-select: none;\n    background-color: transparent;\n    border: $dg-btn-border-width solid transparent;\n    @include dg-button-size($dg-btn-padding-y, $dg-btn-padding-x, $dg-btn-font-size, $dg-btn-line-height, $dg-btn-border-radius);\n    // @include transition($btn-transition);\n\n    &:focus,\n    &.focus {\n        outline: 0;\n        box-shadow: $dg-btn-focus-box-shadow;\n    }\n\n    // Disabled comes first so active can properly restyle\n    &.disabled,\n    &:disabled {\n        opacity: $dg-btn-disabled-opacity;\n        @include dg-box-shadow(none);\n    }\n\n    // &:not(:disabled):not(.disabled):active,\n    // &:not(:disabled):not(.disabled).active {\n    //     @include dg-box-shadow($dg-btn-active-box-shadow);\n\n    //     &:focus {\n    //         @include box-shadow($dg-btn-focus-box-shadow, $dg-btn-active-box-shadow);\n    //     }\n    // }\n}\n\n// Future-proof disabling of clicks on `<a>` elements\na.btn.disabled,\nfieldset:disabled a.btn {\n    pointer-events: none;\n}\n\n//\n// Alternate buttons\n//\n\n@each $color, $value in $dg-theme-colors {\n    .dg-btn.dg-btn-outline-#{$color} {\n        @include dg-button-outline-variant($value, white);\n    }\n    .dg-btn.dg-btn-#{$color} {\n        @include dg-button-variant($value, $value);\n        &:hover {\n            box-shadow: 0 2px 5px 1px rgba($value, 0.6);\n        }\n    }\n}\n\n// @each $color, $value in $theme-colors {\n//     .btn-outline-#{$color} {\n//         @include dg-utton-outline-variant($value);\n//     }\n// }\n\n//\n// Link buttons\n//\n\n// Make a button look and behave like a link\n// .btn-link {\n//     font-weight: $font-weight-normal;\n//     color: $link-color;\n//     text-decoration: $link-decoration;\n\n//     @include hover() {\n//         color: $link-hover-color;\n//         text-decoration: $link-hover-decoration;\n//     }\n\n//     &:focus,\n//     &.focus {\n//         text-decoration: $link-hover-decoration;\n//         box-shadow: none;\n//     }\n\n//     &:disabled,\n//     &.disabled {\n//         color: $btn-link-disabled-color;\n//         pointer-events: none;\n//     }\n\n//     // No need for an active state here\n// }\n\n//\n// Button Sizes\n//\n\n// .btn-lg {\n//     @include button-size($btn-padding-y-lg, $btn-padding-x-lg, $btn-font-size-lg, $btn-line-height-lg, $btn-border-radius-lg);\n// }\n\n.dg-btn-xsm {\n    @include dg-button-size(\n        $dg-btn-padding-y-xsm,\n        $dg-btn-padding-x-xsm,\n        $dg-btn-font-size-xsm,\n        $dg-btn-line-height-xsm,\n        $dg-btn-border-radius-xsm\n    );\n    .dg-icon {\n        font-size: $dg-btn-font-size-xsm;\n    }\n}\n\n.dg-btn-sm {\n    @include dg-button-size(\n        $dg-btn-padding-y-sm,\n        $dg-btn-padding-x-sm,\n        $dg-btn-font-size-sm,\n        $dg-btn-line-height-sm,\n        $dg-btn-border-radius-sm\n    );\n    .dg-icon {\n        font-size: $dg-btn-font-size-sm;\n    }\n}\n\n.dg-btn-md {\n    @include dg-button-size(\n        $dg-btn-padding-y-md,\n        $dg-btn-padding-x-md,\n        $dg-btn-font-size-md,\n        $dg-btn-line-height-md,\n        $dg-btn-border-radius-md\n    );\n    .dg-icon {\n        font-size: $dg-btn-font-size-md;\n    }\n}\n\n.dg-btn-lg {\n    @include dg-button-size(\n        $dg-btn-padding-y-lg,\n        $dg-btn-padding-x-lg,\n        $dg-btn-font-size-lg,\n        $dg-btn-line-height-lg,\n        $dg-btn-border-radius-lg\n    );\n    .dg-icon {\n        font-size: $dg-btn-font-size-lg;\n    }\n}\n\n.dg-btn-xlg {\n    @include dg-button-size(\n        $dg-btn-padding-y-xlg,\n        $dg-btn-padding-x-xlg,\n        $dg-btn-font-size-xlg,\n        $dg-btn-line-height-xlg,\n        $dg-btn-border-radius-xlg\n    );\n    .dg-icon {\n        font-size: $dg-btn-font-size-xlg;\n    }\n}\n\n//\n// Block button\n//\n\n// .dg-btn-block {\n//     display: block;\n//     width: 100%;\n\n//     // Vertically space out multiple block buttons\n//     + .btn-block {\n//         margin-top: $dg-btn-block-spacing-y;\n//     }\n// }\n\n// Specificity overrides\ninput[type='submit'],\ninput[type='reset'],\ninput[type='button'] {\n    &.dg-btn-block {\n        width: 100%;\n    }\n}\n\n.dg-btn.dg-btn-round {\n    @include dg-button-radius-variant(8888px);\n}\n","// Button variants\n@mixin dg-button-variant(\n    $background,\n    $border,\n    $hover-background: darken($background, 7.5%),\n    $hover-border: darken($border, 10%),\n    $active-background: darken($background, 10%),\n    $active-border: darken($border, 12.5%)\n) {\n    color: dg-color-yiq($background);\n    @include dg-gradient-bg($background);\n    border-color: $border;\n    @include dg-box-shadow($dg-btn-box-shadow);\n\n    @include hover() {\n        color: dg-color-yiq($hover-background);\n        @include dg-gradient-bg($hover-background);\n        border-color: $hover-border;\n    }\n\n    &:focus,\n    &.focus {\n        color: dg-color-yiq($hover-background);\n        @include dg-gradient-bg($hover-background);\n        border-color: $hover-border;\n        // Avoid using mixin so we can pass custom focus shadow properly\n        @if $dg-enable-shadows {\n            box-shadow: $dg-btn-box-shadow, 0 0 0 $dg-btn-focus-width rgba(mix(dg-color-yiq($background), $border, 15%), 0.5);\n        } @else {\n            box-shadow: 0 0 0 $dg-btn-focus-width rgba(mix(color-yiq($background), $border, 15%), 0.5);\n        }\n    }\n\n    // Disabled comes first so active can properly restyle\n    &.disabled,\n    &:disabled {\n        color: color-yiq($background);\n        background-color: $background;\n        border-color: $border;\n        // Remove CSS gradients if they're enabled\n        @if $dg-enable-gradients {\n            background-image: none;\n        }\n    }\n\n    &:not(:disabled):not(.disabled):active,\n    &:not(:disabled):not(.disabled).active,\n    .show > &.dropdown-toggle {\n        color: color-yiq($active-background);\n        background-color: $active-background;\n        // @if $enable-gradients {\n        //     background-image: none; // Remove the gradient for the pressed/active state\n        // }\n        border-color: $active-border;\n\n        // &:focus {\n        //     // Avoid using mixin so we can pass custom focus shadow properly\n        //     @if $enable-shadows and $btn-active-box-shadow != none {\n        //         box-shadow: $btn-active-box-shadow, 0 0 0 $btn-focus-width rgba(mix(color-yiq($background), $border, 15%), 0.5);\n        //     } @else {\n        //         box-shadow: 0 0 0 $btn-focus-width rgba(mix(color-yiq($background), $border, 15%), 0.5);\n        //     }\n        // }\n    }\n}\n\n@mixin dg-button-outline-variant(\n    $color,\n    $color-hover: color-yiq($color),\n    $active-background: $color,\n    $active-border: $color,\n    $btn-focus-width: 0px\n) {\n    color: $color;\n    border-color: $color;\n\n    @include hover() {\n        color: $color-hover;\n        background-color: $active-background;\n        border-color: $active-border;\n    }\n\n    &:focus,\n    &.focus {\n        box-shadow: 0 0 0 $btn-focus-width rgba($color, 0.5);\n    }\n\n    &.disabled,\n    &:disabled {\n        color: $color;\n        background-color: transparent;\n    }\n\n    &:not(:disabled):not(.disabled):active,\n    &:not(:disabled):not(.disabled).active,\n    .show > &.dropdown-toggle {\n        color: color-yiq($active-background);\n        background-color: $active-background;\n        border-color: $active-border;\n\n        &:focus {\n            // Avoid using mixin so we can pass custom focus shadow properly\n\n            box-shadow: 0 0 0 $btn-focus-width rgba($color, 0.5);\n        }\n    }\n}\n\n// Button sizes\n@mixin dg-button-size($padding-y, $padding-x, $font-size, $line-height, $border-radius) {\n    padding: $padding-y $padding-x;\n    font-size: $font-size;\n    line-height: $line-height;\n    // Manually declare to provide an override to the browser default\n    @include dg-border-radius($border-radius, 0);\n}\n\n@mixin dg-button-radius-variant($border-radius) {\n    border-radius: $border-radius;\n}\n","@mixin dg-border-radius($radius: $border-radius, $fallback-border-radius: false) {\n    @if $dg-enable-rounded {\n        border-radius: $radius;\n    } @else if $fallback-border-radius != false {\n        border-radius: $fallback-border-radius;\n    }\n}\n\n@mixin dg-border-top-radius($radius) {\n    @if $dg-enable-rounded {\n        border-top-left-radius: $radius;\n        border-top-right-radius: $radius;\n    }\n}\n\n@mixin dg-border-right-radius($radius) {\n    @if $dg-enable-rounded {\n        border-top-right-radius: $radius;\n        border-bottom-right-radius: $radius;\n    }\n}\n\n@mixin dg-border-bottom-radius($radius) {\n    @if $dg-enable-rounded {\n        border-bottom-right-radius: $radius;\n        border-bottom-left-radius: $radius;\n    }\n}\n\n@mixin dg-border-left-radius($radius) {\n    @if $dg-enable-rounded {\n        border-top-left-radius: $radius;\n        border-bottom-left-radius: $radius;\n    }\n}\n\n@mixin dg-border-top-left-radius($radius) {\n    @if $dg-enable-rounded {\n        border-top-left-radius: $radius;\n    }\n}\n\n@mixin dg-border-top-right-radius($radius) {\n    @if $dg-enable-rounded {\n        border-top-right-radius: $radius;\n    }\n}\n\n@mixin dg-border-bottom-right-radius($radius) {\n    @if $dg-enable-rounded {\n        border-bottom-right-radius: $radius;\n    }\n}\n\n@mixin dg-border-bottom-left-radius($radius) {\n    @if $dg-enable-rounded {\n        border-bottom-left-radius: $radius;\n    }\n}\n","@mixin dg-box-shadow($shadow...) {\n    @if $dg-enable-shadows {\n        $result: ();\n\n        @if (length($shadow) == 1) {\n            // We can pass `@include box-shadow(none);`\n            $result: $shadow;\n        } @else {\n            // Filter to avoid invalid properties for example `box-shadow: none, 1px 1px black;`\n            @for $i from 1 through length($shadow) {\n                @if nth($shadow, $i) != 'none' {\n                    $result: append($result, nth($shadow, $i), 'comma');\n                }\n            }\n        }\n        @if (length($result) > 0) {\n            box-shadow: $result;\n        }\n    }\n}\n","@mixin hover() {\n    &:hover {\n        @content;\n    }\n}\n\n@mixin hover-focus() {\n    &:hover,\n    &:focus {\n        @content;\n    }\n}\n\n@mixin plain-hover-focus() {\n    &,\n    &:hover,\n    &:focus {\n        @content;\n    }\n}\n\n@mixin hover-focus-active() {\n    &:hover,\n    &:focus,\n    &:active {\n        @content;\n    }\n}\n","// Gradients\n\n@mixin dg-gradient-bg($color) {\n    @if $dg-enable-gradients {\n        background: $color linear-gradient(180deg, mix($dg-body-bg, $color, 15%), $color) repeat-x;\n    } @else {\n        background-color: $color;\n    }\n}\n\n// Horizontal gradient, from left to right\n//\n// Creates two color stops, start and end, by specifying a color and position for each color stop.\n@mixin dg-gradient-x($start-color: $gray-700, $end-color: $gray-800, $start-percent: 0%, $end-percent: 100%) {\n    background-image: linear-gradient(to right, $start-color $start-percent, $end-color $end-percent);\n    background-repeat: repeat-x;\n}\n\n// Vertical gradient, from top to bottom\n//\n// Creates two color stops, start and end, by specifying a color and position for each color stop.\n@mixin dg-gradient-y($start-color: $gray-700, $end-color: $gray-800, $start-percent: 0%, $end-percent: 100%) {\n    background-image: linear-gradient(to bottom, $start-color $start-percent, $end-color $end-percent);\n    background-repeat: repeat-x;\n}\n\n@mixin dg-gradient-directional($start-color: $gray-700, $end-color: $gray-800, $deg: 45deg) {\n    background-image: linear-gradient($deg, $start-color, $end-color);\n    background-repeat: repeat-x;\n}\n\n@mixin dg-gradient-x-three-colors($start-color: $blue, $mid-color: $purple, $color-stop: 50%, $end-color: $red) {\n    background-image: linear-gradient(to right, $start-color, $mid-color $color-stop, $end-color);\n    background-repeat: no-repeat;\n}\n\n@mixin dg-gradient-y-three-colors($start-color: $blue, $mid-color: $purple, $color-stop: 50%, $end-color: $red) {\n    background-image: linear-gradient($start-color, $mid-color $color-stop, $end-color);\n    background-repeat: no-repeat;\n}\n\n@mixin dg-gradient-radial($inner-color: $gray-700, $outer-color: $gray-800) {\n    background-image: radial-gradient(circle, $inner-color, $outer-color);\n    background-repeat: no-repeat;\n}\n\n@mixin dg-gradient-striped($color: rgba($white, 0.15), $angle: 45deg) {\n    background-image: linear-gradient(\n        $angle,\n        $color 25%,\n        transparent 25%,\n        transparent 50%,\n        $color 50%,\n        $color 75%,\n        transparent 75%,\n        transparent\n    );\n}\n",".dg-navbar {\n    position: fixed;\n    top: 0;\n    left: 0;\n    right: 0;\n    // height: $dg-navbar-height;\n    display: flex;\n    align-items: center;\n    padding: 0.719rem 1.25rem;\n    justify-content: space-between;\n    font-size: $dg-font-size-md;\n    z-index: 100;\n    .nav.nav-spacer {\n        display: flex;\n        visibility: hidden;\n        width: 0;\n        margin: 0;\n        flex: 0 0 auto;\n    }\n    .navbar-brand {\n        display: inline-flex;\n        align-items: center;\n        margin-right: 1rem;\n\n        .logo {\n            margin-right: 10px;\n            width: 30px;\n            height: 30px;\n        }\n\n        .title {\n            font-weight: 600;\n            color: $dg-white;\n        }\n    }\n\n    .navbar-toggler {\n        margin-left: 10px;\n        display: none;\n    }\n\n    .nav {\n        display: flex;\n        a {\n            padding: 6px 18px;\n            margin-right: 8px;\n\n            &.active {\n                border-radius: 4px;\n            }\n        }\n    }\n\n    .spacer {\n        flex: 1;\n    }\n\n    .action-items {\n        di