           }\n\n                &.active {\n                    background: hsla(0, 0%, 100%, 0.15);\n                }\n            }\n        }\n    }\n}\n\n.dg-theme-default {\n    .dg-navbar {\n        @include dg-navbar-theme($dg-navbar-background-default, $dg-navbar-box-shadow-default, $dg-navbar-color-default);\n\n        .nav {\n            a {\n                &:hover {\n                    color: $dg-primary;\n                }\n\n                &.active {\n                    color: $dg-primary;\n                    background: rgba($dg-primary, 0.12);\n                }\n            }\n        }\n    }\n}\n",".dg-menu {\n    overflow-x: hidden;\n\n    .menu-group {\n        cursor: pointer;\n        .group-header {\n            display: flex;\n            align-items: center;\n            .group-title {\n                flex: 1 1 0;\n                padding: $dg-sidebar-menu-item-space-y 30px;\n                color: $dg-gray-600;\n                padding-right: 0;\n            }\n            .group-arrow {\n                margin-right: 30px;\n                transition: all 0.5s;\n            }\n        }\n        .group-body {\n            margin-left: $dg-sidebar-item-indent;\n            max-height: 0;\n            opacity: 0;\n            overflow: hidden;\n            transition: all 0.5s;\n        }\n\n        .menu-item {\n            margin-left: $dg-sidebar-item-indent;\n        }\n        &.group-group--open {\n            & > .group-body {\n                max-height: 4000px;\n                opacity: 1;\n            }\n            & > .group-arrow {\n                transform: rotate(90deg);\n            }\n        }\n    }\n\n    .menu-item {\n        padding: 0px $dg-sidebar-menu-item-space-x/2 0 $dg-sidebar-menu-item-space-x;\n        &.active {\n            padding-right: $dg-sidebar-menu-item-space-x/2 - 3px;\n        }\n        display: block;\n        // a {\n        //     color: $dg-gray-800;\n        // }\n\n        &.active {\n 